@import '../../../../utils/var.scss';

.component {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid $backgroundColor2;
}
.container {
  display: flex;
  flex-direction: row;
  width: 327px;
  flex-wrap: wrap;
  gap: 8px 10px;
  margin-bottom: 10px;
}
.title {
  @include defaultPM();
  @include fontRegular(14px, 18px, #939598);
  margin: 10px 0 8px 0;
}
