.component {
  display: flex;
  align-self: center;
  width: 343px;
  height: 253px;
  margin-top: 29px;
}
.container {
  display: flex;
  justify-content: center;
  width: 343px;
  height: 253px;
  position: relative;
}
.ellipse {
  width: 125px;
  height: 125px;
  overflow: hidden;
  background-position: center;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 10;
}
.point {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 20;
}
.rect {
  display: flex;
  width: 328px;
  height: 200px;
  border: 3px solid #ee3d48;
  border-radius: 8px;
  margin-top: 11px;
  justify-content: center;
  align-items: center;
  z-index: 50;
}
.pic {
  width: 315px;
  height: 188px;
  border-radius: 8px;
}
